import React, { useEffect, useRef } from "react";
import "./Home.css";
import { init } from "ityped";
const Home = () => {
  const textref = useRef();
  useEffect(() => {
    init(textref.current, {
      showCursor: true,
      backDelay: 1000,
      strings: ["Developer", "Designer", "Gamer"],
    });
  }, []);
  return (
    <div className="homepage" id="home">
        <div className="outline_text1">HOME</div>
      <div className="left"></div>
      <div className="right">
        <div className="wrapper">
          <h3 className="h333">
            I am a <span ref={textref}></span>
          </h3>
          <h2 className="h222">Hi There, I'm</h2>
          <h1 className="h111">Karan Bhati</h1>
        </div>
       
        <a href="#about"></a>
      </div>
    </div>
  );
};

export default Home;
