import React, { useEffect } from "react";
import "./About.css";
import AOS from "aos";
import "aos/dist/aos.css";
const About = () => {
  useEffect(() => {
    AOS.init({ duration: 6000 });
    AOS.refresh();
  }, []);

  return (
    <div className="aboutpage" id="about">
      <div className="wave">
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320">
          <path
            fill="#17141d"
            fill-opacity="1"
            d="M0,320L60,293.3C120,267,240,213,360,208C480,203,600,245,720,256C840,267,960,245,1080,229.3C1200,213,1320,203,1380,197.3L1440,192L1440,0L1380,0C1320,0,1200,0,1080,0C960,0,840,0,720,0C600,0,480,0,360,0C240,0,120,0,60,0L0,0Z"
          ></path>
        </svg>
        <h2>About</h2>

        <div className="flex_container">
        <div className="card" id="card1">
            <div className="headingcard">Education</div>
            <div className="text">
              Completed my <strong>Bachelors</strong> Degree in{" "}
              <em>Computer Engineering </em>
              From{" "}
              <strong>
                Alpha College of Engineering and Technology in year{" "}
                <em>2021.</em>
              </strong>
            </div>
          </div>
        
        
          <div className="card" id="card3">
            <div className="headingcard">Skills</div>
            <div className="text-container">
              <div className="text-items">
                <div className="skill1">Html</div>
              </div>
              <div className="text-items">
                <div className="skill2">CSS</div>
              </div>
              <div className="text-items">
                <div className="skill3">JavaScript</div>
              </div>
              <div className="text-items">
                <div className="skill4">Android</div>
              </div>
              <div className="text-items">
                <div className="skill5">Reactjs</div>
              </div>
              <div className="text-items">
                <div className="skill6">java</div>
              </div>
            </div>
          </div>
          
          <div className="card" id="card2">
            <div className="headingcard">Goal</div>
            <div className="text">
             <strong>"To BECOME A SUCCESSFULL FULLSTACK DEVELOPER "</strong> 
            </div>
          </div>
         
        </div>
      </div>
    </div>
  );
};

export default About;
