import React, { useState } from "react";
import './Nav2.css'
const Nav2 = () => {
const [home,setactive] = useState(false);
const [about,setabout] = useState(false);
const [contact,setcontact] = useState(false);
const navchange = () =>{
  setabout(false)
  setcontact(false)
    let a = "active";
    setactive(a);

}
const aboutchange = () =>{
  setactive (false)
  setcontact(false)
  let a ="active"
  setabout(a)
}

const contactchange = () =>{
  setactive(false);
  setabout(false);
  let a ="active"
  setcontact(a)
}
  return (
    <div>
   
   
        <nav className="navbar" id="navbar">
          <li className="nav-items">
            <a className={home} onClick={navchange}  href="#home">HOME</a>
          </li>
          <li className="nav-items">
            <a className={about} onClick={aboutchange} href="#about">ABOUT</a>
          </li>
          <li className="nav-items">
            <a className={contact} onClick={contactchange} exact href="#contact">CONTACT</a>
          </li>
          
        </nav>

    </div>
  );
};

export default Nav2;
